import * as XLSX from 'xlsx';
import { useState, useCallback } from 'react';

import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import LoadingButton from '@mui/lab/LoadingButton';
import {
  Chip,
  Table,
  Select,
  Checkbox,
  MenuItem,
  TableRow,
  TableBody,
  TableCell,
  TextField,
  Typography,
  TableContainer,
} from '@mui/material';

import { paths } from 'src/routes/paths';
import { useRouter } from 'src/routes/hooks';

import axiosInstance, { endpoints } from 'src/utils/axios';

import { useGetSymptoms } from 'src/api';

import { Upload } from 'src/components/upload';
import Scrollbar from 'src/components/scrollbar';
import { useSnackbar } from 'src/components/snackbar';
import {
  useTable,
  TableHeadCustom,
  TableSelectedAction,
  TablePaginationCustom,
} from 'src/components/table';

// ----------------------------------------------------------------------
const TABLE_HEAD = [
  { label: 'Name', width: 'calc(100%/6)' },
  { label: 'ICD_code', width: 'calc(100%/6)' },
  { label: 'Parent', width: 'calc(100%/6)' },
  { label: 'Group', width: 'calc(100%/6)' },
  { label: 'symptoms', width: 'calc(100%/6)' },
];
export default function NewEditManyForm() {
  const router = useRouter();

  const table = useTable({ defaultOrderBy: 'code' });

  // const { categories } = useGetCategories(); /// edit
  const { tableData } = useGetSymptoms(); /// edit

  const { enqueueSnackbar } = useSnackbar();

  const [data, setData] = useState([]);

  const handleArabicInputChange = (index, event) => {
    const arabicRegex = /^[\u0600-\u06FF0-9\s!@#$%^&*_\-().]*$/;

    if (arabicRegex.test(event.target.value)) {
      setData((prev) => {
        const updated = [...prev];
        updated[index] = { ...updated[index], [event.target.name]: event.target.value };
        return updated;
      });
    }
  };

  const handleEnglishInputChange = (index, event) => {
    const englishRegex = /^[a-zA-Z0-9\s,@#$!*_\-&^%.()]*$/;

    if (englishRegex.test(event.target.value)) {
      setData((prev) => {
        const updated = [...prev];
        updated[index] = { ...updated[index], [event.target.name]: event.target.value };
        return updated;
      });
    }
  };

  const handleSelect = (index, event) => {
    setData((prev) => {
      const updated = [...prev];
      updated[index] = { ...updated[index], [event.target.name]: event.target.value };
      return updated;
    });
  };

  const handleSelectMany = (event) => {
    setData((prev) => {
      const updated = [...prev];

      table.selected.forEach((item) => {
        updated[item] = { ...updated[item], [event.target.name]: event.target.value };
      });

      return updated;
    });
  };

  const handleDrop = useCallback((acceptedFiles) => {
    const file = acceptedFiles[0];

    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const results = e.target.result;
        const workbook = XLSX.read(results, { type: 'binary' });

        const workSheets = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(workSheets);
        setData(jsonData);
      };
      reader.readAsBinaryString(file);
    }
  }, []);

  const handleCreate = async () => {
    // const isFormValid = data.every(
    //   (one) => one.name_english && one.name_arabic && one.category && one.symptoms.length
    // );

    // if (!isFormValid) {
    //   alert('Please fill in all required fields.');
    //   return;
    // }
    try {
      const uploadRec = await axiosInstance.post(endpoints.upload_records.all, {
        type: 'diseases',
        mustUpload: data.length,
      });
      const chunkSize = 100;
      const totalChunks = Math.ceil(data.length / chunkSize);

      const promises = Array.from({ length: totalChunks }, (_, index) => {
        const startIndex = index * chunkSize;
        const endIndex = Math.min(startIndex + chunkSize, data.length);
        const chunkData = data.slice(startIndex, endIndex);

        const insertedDataPromise = axiosInstance.post(
          endpoints.diseases.many,
          chunkData.map((one) => ({ ...one, upload_record: uploadRec.data._id }))
        );
        return insertedDataPromise.then((insertedData) =>
          axiosInstance.patch(endpoints.upload_records.one(uploadRec.data._id), {
            uploaded: insertedData.data?.length,
          })
        );
      });

      await Promise.all(promises);

      router.push(paths.superadmin.tables.diseases.root); /// edit
    } catch (e) {
      enqueueSnackbar(e, { variant: 'error' });
    }
  };

  return (
    <Card sx={{ p: 3 }}>
      <Box
        sx={{
          px: 5,
          pb: 2,
        }}
      >
        <Upload
          name="excel"
          accept=".xlsx, .xls"
          label="upload excel"
          onDrop={(info) => handleDrop(info)}
        />
      </Box>
      {data.length > 0 && (
        <TableContainer>
          <TableSelectedAction
            numSelected={table.selected.length}
            rowCount={data.length}
            onSelectAllRows={(checked) =>
              table.onSelectAllRows(
                checked,
                data.map((row, index, idx) => index)
              )
            }
            action={
              <>
                {/* <Typography sx={{ fontSize: 14, fontWeight: 600, pr: 1 }}>Category:</Typography>
                <Select
                  variant="filled"
                  sx={{ width: '20%', border: '1px solid gray' }}
                  size="small"
                  name="category"
                  // label="category"
                  onChange={handleSelectMany}
                >
                  {categories.map((category, idx) => (
                    <MenuItem lang="ar" key={idx} value={category._id}>
                      {category.name_english}
                    </MenuItem>
                  ))}
                </Select> */}
                <Typography sx={{ fontSize: 14, fontWeight: 600, pr: 1 }}>Symptoms:</Typography>
                <Select
                  many
                  variant="filled"
                  sx={{ width: '20%', border: '1px solid gray' }}
                  size="small"
                  name="symptoms"
                  // label="category"
                  onChange={handleSelectMany}
                >
                  {tableData.map((symptom, idx) => (
                    <MenuItem lang="ar" key={idx} value={symptom._id}>
                      {symptom.name_english}
                    </MenuItem>
                  ))}
                </Select>
              </>
            }
            color="primary"
          />

          <Scrollbar>
            <Table size="medium">
              <TableHeadCustom
                headLabel={TABLE_HEAD}
                rowCount={data.length}
                numSelected={table.selected.length}
                onSelectAllRows={(checked) =>
                  table.onSelectAllRows(
                    checked,
                    data.map((row, index, idx) => index)
                  )
                }
              />

              <TableBody>
                {data
                  .slice(
                    table.page * table.rowsPerPage,
                    table.page * table.rowsPerPage + table.rowsPerPage
                  )
                  .map((one, index) => (
                    <TableRow key={index} hover selected={table.selected.includes(index)}>
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={table.selected.includes(index)}
                          onClick={() => table.onSelectRow(index)}
                        />
                      </TableCell>

                      <TableCell align="center">
                        <TextField
                          size="small"
                          variant="filled"
                          onChange={(e) => handleEnglishInputChange(index, e)}
                          value={one.Name}
                          name="Name"
                        />
                      </TableCell>
                      <TableCell align="center">
                        <TextField
                          size="small"
                          variant="filled"
                          onChange={(e) => handleArabicInputChange(index, e)}
                          value={one.ICD_code}
                          name="ICD_code"
                        />
                      </TableCell>
                      <TableCell align="center">
                        <TextField
                          size="small"
                          variant="filled"
                          onChange={(e) => handleArabicInputChange(index, e)}
                          value={one.Parent}
                          name="Parent"
                        />
                      </TableCell>
                      <TableCell align="center">
                        <TextField
                          size="small"
                          variant="filled"
                          onChange={(e) => handleArabicInputChange(index, e)}
                          value={one.Group}
                          name="Group"
                        />
                      </TableCell>
                      <TableCell align="center">
                        <Select
                          multiple
                          variant="filled"
                          required
                          value={one.symptoms || []}
                          onChange={(e) => handleSelect(index, e)}
                          sx={{ width: '80%' }}
                          size="small"
                          name="symptoms"
                          renderValue={(selected) => (
                            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                              {selected.map((value, idx) => {
                                const symptom = tableData.find((item) => item._id === value);
                                return (
                                  <Chip
                                    key={idx}
                                    label={symptom.name_english}
                                    style={{ margin: 2 }}
                                  />
                                );
                              })}
                            </div>
                          )}
                        >
                          {tableData.map((symptom, idx) => (
                            <MenuItem lang="ar" key={idx} value={symptom._id}>
                              {/* <Checkbox
                                  checked={one?.symptoms?.includes(symptom._id)}
                                  size="small"
                                  disableRipple
                                /> */}
                              {symptom.name_english}
                            </MenuItem>
                          ))}
                        </Select>
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
            <TablePaginationCustom
              count={data.length}
              page={table.page}
              rowsPerPage={table.rowsPerPage}
              onPageChange={table.onChangePage}
              onRowsPerPageChange={table.onChangeRowsPerPage}
              //
              dense={table.dense}
              onChangeDense={table.onChangeDense}
            />
          </Scrollbar>
        </TableContainer>
      )}

      {data.length > 0 && (
        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
          <LoadingButton onClick={handleCreate} tabIndex={-1} variant="contained">
            Create All
          </LoadingButton>
        </Stack>
      )}
    </Card>
  );
}
